/** @format */

body {
  margin: 0;
  padding: 0;
  background-color: #08b4f3;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
main {
  margin: 0 auto;
  text-align: center;
  color: white;
  font-family: 'Lato', sans-serif;
  height: 1000px;
}

h1 {
  font-size: 23px;
  margin-top: 40px;
  margin-bottom: 16px;
}
p {
  font-size: 14px;
}

.cards {
  display: flex;
  text-align: center;
  width: 80%;
  margin-left: 14.5%;
  color: black;

}


.card{
  width: 230px;
  height: 300px;
  margin-top: 30px;
  margin-right: 30px;
  padding: 20px;
  background-color: aliceblue;
  border-radius: 10px 10px;
  text-align: left;

}
.card > h3{
  margin-top: 22px;
}
.card > p{
  margin-top: 18px;
}

.cards1 {
  display: flex;
  text-align: center;
  width: 80%;
  margin-left: 14.5%;
  color: black;
  gap: 50px;

}

.card1{
  width: 200px;
  height: 200px;
  margin-right: 65px;
  padding: 20px;
  background-color: rgb(233, 239, 245);
  border-radius: 10px 10px;
  text-align: left;

}
.card1 > h3{
  margin-top: 22px;
}
.card1 > p{
  margin-top: 18px;
}
.card1 > span {
  color: #e51a1a;
}

.footer > p{
  margin-top: 20px;
  font-size: 15px;
}
span{
